import styled from "styled-components";

const DescriptionDiv = styled.div`
  min-height: 540px;
`;

const ImageDiv = styled.div`
  width: 100%;
  text-align: center;
  margin-top: 4%;

  img {
    margin: 0;
  }
`;

const ContestDescription = () => {
  return (
    <DescriptionDiv>
      <ImageDiv>
        <img
          src={`${process.env.PUBLIC_URL}/image/Description/contestDescription.png`}
          alt="myroomDescription"
          style={{ width: "100%" }}
        />
      </ImageDiv>
      <p className="pageName"> </p>
      <p style={{ marginTop: "3%" }}>
                 .
      </p>
      <p>
               ,  
           .
      </p>
    </DescriptionDiv>
  );
};

export default ContestDescription;
