import styled from "styled-components";
import "../style/GlobalCSS.scss";
import { useState } from "react";
import MyroomDescription from "./MyroomDescription";
import StoreDescription from "./StoreDescription";
import GameDescription from "./GameDescription";
import ContestDescription from "./ContestDescription";

const FooterDiv = styled.div`
  background-color: #e9e9a1;
`;

const ContentDiv = styled.div`
  width: 50%;
  margin: 0 auto;

  .title {
    text-align: center;
    margin-top: 3%;
    margin-bottom: 10%;
  }

  p {
    margin: 0;
    text-align: center;
  }

  .control,
  .pageName {
    font-weight: 900;
  }
`;

const CategoryBtn = styled.div`
  text-align: center;
  button {
    margin: 4% 5%;
  }
`;

const Footer = () => {
  const [category, setCategory] = useState("myroom");
  return (
    <FooterDiv>
      <ContentDiv>
        <p className="nonDisplay">.</p>
        <h1 className="title">SimFarm </h1>
        <p>
          SimFarm       
           .
        </p>
        <p>      .</p>
        <br />
        <p className="control"> </p>
        <p></p>
        <CategoryBtn>
          <button onClick={() => setCategory("myroom")}></button>
          <button onClick={() => setCategory("store")}></button>
          <button onClick={() => setCategory("game")}></button>
          <button onClick={() => setCategory("contest")}></button>
        </CategoryBtn>
        {category === "myroom" ? <MyroomDescription /> : null}
        {category === "store" ? <StoreDescription /> : null}
        {category === "game" ? <GameDescription /> : null}
        {category === "contest" ? <ContestDescription /> : null}
      </ContentDiv>
    </FooterDiv>
  );
};

export default Footer;
