import React from "react";
import { useCallback, useEffect } from "react";
import { Unity, useUnityContext } from "react-unity-webgl";
import "../../style/GlobalCSS.scss";

const Game = ({onIncrease}) => {
  const {
    unityProvider,
    // sendMessage, // unity    sendMessage 
    addEventListener, // unity -> react 
    removeEventListener, // unity -> react 
    UNSAFE__detachAndUnloadImmediate: detachAndUnloadImmediate,
  } = useUnityContext({
    loaderUrl: "build/Adv_unity.loader.js",
    dataUrl: "build/Adv_unity.data",
    frameworkUrl: "build/Adv_unity.framework.js",
    codeUrl: "build/Adv_unity.wasm",
  });

  const setScoreUp = useCallback((curScore) => {
    onIncrease(curScore);
  }, [onIncrease]);

  useEffect(() => {
    addEventListener("reactScoreUp", setScoreUp);

    return () => {
      // Unity  detachAndUnloadImmediate 
      if (
        unityProvider &&
        detachAndUnloadImmediate &&
        typeof detachAndUnloadImmediate === "function"
      ) {
        detachAndUnloadImmediate().catch((reason) => {
          console.log(reason);
        });
        removeEventListener("reactScoreUp", setScoreUp);
      }
    };
  }, [
    unityProvider,
    detachAndUnloadImmediate,
    addEventListener,
    removeEventListener,
    setScoreUp,
  ]);

  return (
    <div className="wrapping">
      <Unity
        style={{
          width: "960px",
          height: "540px",
          justifySelf: "center",
          alignSelf: "center",
        }}
        unityProvider={unityProvider}
      />
    </div>
  );
};

export default Game;
